body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  width: 100%;
  height: 100%;
}

.App {
  text-align: center;
  /* background-image: url(https://images.unsplash.com/photo-1428908728789-d2de25dbd4e2?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1920&q=80);
  background-size: cover;
  background-repeat: no-repeat; */
  background: #eee;
  width: 100%;
  height: 100%;
}

pre {
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  word-wrap: break-word;
}

.typing {
  width: 90%;
  height: 408px;
  border: 1px solid #303030;
  border-radius: 4px;
  margin: auto;
  margin-top: 50px;
  overflow-y: scroll;
  background-color: white
}

.text {
  font-size: 20px;
  color: gray;
  text-align: left;
  padding: 10px 20px;
  font-weight: 800;
}

.ch.correct {
  color: #1FBD83;
}

.ch.incorrect {
  color: hotpink;
}

.ch.current {
  border-bottom: 2px solid lightseagreen;
}
.keyboard {
  margin: 0;
  padding: 0;
  list-style: none;
  background: rgb(148, 148, 148);
  position: absolute;
  width: 684px;
  margin-top: 20px;
  margin-left: calc(50% - 342px);
  box-sizing: border-box;
}

.keyboard > li {
  float: left;
  margin: 0 5px 5px 0;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: rgb(37, 37, 37);
  color: white;
  border: 1px solid black;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  font-size: 15px;
}

.capslock,
.tab,
.left-shift {
  clear: left;
}

.keyboard .tab,
.keyboard .delete {
  width: 70px;
}

.keyboard .capslock {
  width: 80px;
}

.keyboard .return {
  width: 77px;
}

.keyboard .left-shift {
  width: 95px;
}
.keyboard .right-shift {
  width: 109px;
}

.keyboard > li.lastitem {
  margin-right: 0;
}
.uppercase {
  text-transform: uppercase;
}
.keyboard .space {
  clear: left;
  width: 681px;
}
.on {
  display: none;
}
.keyboard li:hover {
  position: relative;
  top: 1px;
  left: 1px;
  border-color: #e5e5e5;
  cursor: pointer;
}

.keyboard > .pressed {
  background: rgb(37, 37, 37);
  color: lightgreen;
  border: 1px solid lightgreen;
  font-size: 24px;
}

.keyboard > .capslock.pressed {
  font-size: 18px;
}
.back {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: rgba(82, 82, 82, 0.473);
  top: 0;
  left: 0;
}

.messageBox {
  width: 400px;
  height: 200px;
  background-color: white;
  border: 2px solid rgb(235, 79, 79);
  border-radius: 5px;
  position: fixed;
  left: calc(50% - 200px);
  top: calc(50% - 150px);
}


.messageText {
  line-height: 200px;
  color: rgb(235, 79, 79);
  font-size: 20px;
  font-weight: 800;
}

.closeBtn {
  position: absolute;
  right: 25px;
  bottom: 25px;
  width: 60px;
  height: 20px;
  line-height: 20px;
  background: rgb(235, 79, 79);
  color: white;
  cursor: pointer;
  border-radius: 5px;
}
